.dubio-input-container {
  border: 1px solid #908e8e;
  border-radius: 9px;
  padding: 2px 12px;
  width: fit-content;
  position: relative;
  label.dubio-input-label {
    color: #908e8e;
    position: absolute;
    top: -22px;
    left: 7px;
    z-index: 1;
    background: white;
    padding: 8px;
    font-size: 0.8rem;
  }
  .ant-select-selector {
    border: none !important;
  }
}
